<template>
  <div class="appBox">
    <v-card
      v-if="modalOpen[0]"
      class="popUpBox"
    >
      <v-list-item three-line>
        <v-list-item-content>
          <div class="text-overline mb-4">
             
          </div>
          <v-list-item-title class="text-h5 mb-1">
            {{arrayBlnck[modalOpen[1]]['title']}}
          </v-list-item-title>
          <v-list-item-subtitle>{{arrayBlnck[modalOpen[1]]['content']}}</v-list-item-subtitle>
        </v-list-item-content>
        <img :src="arrayBlnck[modalOpen[1]]['image']" class="cardImg">
      </v-list-item>
      <v-card-actions>
        <v-btn
          class="cardBtn"
          outlined
          rounded
          text
          @click="modalClose"
        >
          
        </v-btn>
      </v-card-actions>
    </v-card>
    <div class='bigBox'>
      <div>
        <div class="card" v-for='(e, i) in arrayBlnck' :key='i'>
          <img class='userImg' :src='e.image' alt='' />
          <h4 class='catName' @click="modalOpenFunc(e, e.id)">{{ e.title }}</h4>
          <p>{{ e.price }} </p>
          <v-btn
            class="ma-2"
            text
            icon
            color="blue lighten-2"
            @click="number(i)"
          >
            <v-icon>mdi-thumb-up</v-icon>
        </v-btn>
          <span>: {{ declaration[i] }}</span>
        </div>
      </div>
      <infinite-loading @infinite='infiniteHandler' spinner="waveDots"></infinite-loading>
    </div>
  </div>
</template>

<script>
  import InfiniteLoading from 'vue-infinite-loading'

  export default {
    name: 'CatView',
    data () {
      //   
      return {
        arrayBlnck: [], //  
        aNumber: true, //  
        roomNumber: 0,
        currnetPage: 0, //  
        declaration: [0, 0, 0, 0],
        modalOpen: [false, 0],
        nav: ['Home', 'cat', 'About'],
        isActive: false,
      }
    },
    methods: {
      modalClose () {
        this.$set(this.modalOpen, 0, false)
      },
      modalOpenFunc (e, id) {
        this.$set(this.modalOpen, 0, true)
        this.$set(this.modalOpen, 1, id)
      },
      number (i) {
        if (this.aNumber) {
          for (let j = 0; j < this.roomNumber; j++) {
            this.declaration.push(i)
            this.aNumber = false
          }
        }
        // console.log(this.declaration);
        this.$set(this.declaration, i, ++this.declaration[i])
      },
      infiniteHandler ($state) {
        this.$axios.post('/api/data', { ok: 'ok' }).then(res => {
          console.log(res.data)
          setTimeout(() => {
            const temp = []
            console.log(this.arrayBlnck.length)
            for (let i = this.arrayBlnck.length; i <= this.arrayBlnck.length + 3; i++) {
              temp.push(res.data[i])
            }
            console.log(temp)
            if (temp.slice(-1)[0] === undefined) {
              if (temp[0] === undefined) {
                temp.splice(0, 4)
                $state.complete()
              } else if (temp[1] === undefined) {
                temp.splice(1, 3)
                this.arrayBlnck = this.arrayBlnck.concat(temp)
                this.aNumber = true
                $state.loaded()
              } else if (temp[2] === undefined) {
                temp.splice(2, 2)
                this.arrayBlnck = this.arrayBlnck.concat(temp)
                this.aNumber = true
                $state.loaded()
              } else {
                temp.splice(3, 1)
                this.arrayBlnck = this.arrayBlnck.concat(temp)
                this.aNumber = true
                $state.loaded()
              }
            } else {
              this.arrayBlnck = this.arrayBlnck.concat(temp)
              this.aNumber = true
              this.roomNumber += 1
              $state.loaded()
            }
          }, 1000)
        })
      },
      plusBtn () {
        console.log('a')
      },
    },
    components: {
      // appMyModal: DelPopup,
      InfiniteLoading,
    },
  }
</script>

<style lang='scss'>
  @import '@/assets/scss/app.scss'
</style>
